"use client";

import { useState, useEffect } from "react";

const statuses = {
  analyzing: {
    label: "Analyzing Transaction",
    color: "bg-sky-100 text-sky-500",
  },
  success: {
    label: "Transaction Safe",
    color: "bg-green-100 text-green-500",
  },
  warning: {
    label: "Transaction Warning",
    color: "bg-red-100 text-red-500",
  },
} as const;

const FamilyStatusButton = () => {
  const [status, setStatus] = useState<keyof typeof statuses>("analyzing");

  const cycleStatus = () => {
    setTimeout(() => {
      setStatus("success");
    }, 1800);
    setTimeout(() => {
      setStatus("analyzing");
    }, 3200);
    setTimeout(() => {
      setStatus("warning");
    }, 4800);
    setTimeout(() => {
      setStatus("analyzing");
      setTimeout(cycleStatus, 0);
    }, 6400);
  };

  useEffect(() => {
    cycleStatus();
  }, []);

  return (
    <div
      className={`rounded-full font-medium px-6 py-3 ${statuses[status].color}`}
    >
      <span>{statuses[status].label}</span>
    </div>
  );
};

export default FamilyStatusButton;
